$products__p1_height : 207px;
$products__p2_height : 207px;
$products__p3_height : 207px;
$products__p4_height : 207px;
$products__p5_height : 207px;
$products__p6_height : 207px;

body, html{
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  font-family: "微软雅黑";

  min-width: 1000px;
}

div#products{
  position: relative;
  width: 100%;
  height: 100%;

  //background-color: #f1f2f3;
  overflow-y: scroll;

  > img{
    position: absolute;
    width: 600px;
    height: auto;

    top: 47px;
    left: 50%;
    margin-left: -300px;

    z-index: 1;
  }

  section{
    position: absolute;
    display: block;
    box-sizing: border-box;
    background-color: #e9e9e9;
    width: 370px;
    z-index: 10;

    -moz-border-radius: 7px;
    -webkit-border-radius: 7px;
    border-radius:7px;

    box-shadow:2px 2px 5px 1px #aaa;
    -webkit-box-shadow:2px 2px 5px 1px #aaa;
    -moz-box-shadow:2px 2px 5px 1px #aaa;

    filter:alpha(opacity=87);
    -moz-opacity:0.87;
    opacity:0.87;

    > h3{
      margin: 0;
      padding: 9px 0 9px 21px;
      background-color: #e24a1e;
      color: #fff;
      font: 18px/18px "微软雅黑";
      width: 199px;
      -moz-border-radius: 7px;      /* Gecko browsers */
      -webkit-border-radius: 7px;   /* Webkit browsers */
      border-radius:7px;            /* W3C syntax */
    }

    div.text{
      padding: 0 17px 0;
      text-align: justify;
      text-indent: 2em;
    }
  }

  #p1{
    top: 70px;
    left: 10px;
    height: $products__p1_height;
  }

  #p2{
    top: $products__p1_height + 90px;
    left: 10px;
    height: $products__p2_height;
  }

  #p3{
    top: $products__p1_height + $products__p2_height + 110px;
    left: 10px;
    height: $products__p3_height;
  }

  #p4{
    top: 70px;
    right: 10px;
    height: $products__p4_height;
  }

  #p5{
    top: $products__p4_height + 90px;
    right: 10px;
    height: $products__p5_height;
  }

  #p6{
    top: $products__p4_height + $products__p5_height + 110px;
    right: 10px;
    height: $products__p6_height;
  }
}
